// Based on https://github.com/ZeeCoder/react-resize-observer/blob/master/src/index.js

import * as React from "react"
import { RefObject, useState, useRef, useCallback, useEffect } from "react"
import ResizeObserverPoly from "resize-observer-polyfill"
import { Size } from "../../render/types/Size"

export function useMeasureSize(
    ref: RefObject<HTMLElement>,
    observe: boolean = false,
    skipHook: boolean = false,
    initial: Size = { width: 0, height: 0 }
): Size {
    const [size, setSize] = useState<Size>(initial)
    const observer = useRef<ResizeObserverPoly>()

    const cleanup = useCallback(() => {
        if (observer.current && ref.current) {
            observer.current.unobserve(ref.current)
            observer.current.disconnect()
        }
    }, [observer.current, ref.current])

    const onResize = useCallback(() => {
        if (!ref.current) return
        if (!observer.current) return

        const element = ref.current as HTMLDivElement
        const height = element.offsetHeight
        const width = element.offsetWidth

        setSize({
            width,
            height,
        })
    }, [observe, observer.current, ref.current])

    useEffect(() => {
        if (skipHook) {
            return
        }
        if (ref.current) {
            if (observe) {
                observer.current = new ResizeObserverPoly(onResize)
            }
            const element = ref.current
            const height = element.offsetHeight
            const width = element.offsetWidth
            if (observer.current) {
                observer.current.observe(ref.current)
            }
            setSize({
                width,
                height,
            })
        }

        return cleanup
    }, [ref.current, observe, skipHook])

    return skipHook ? initial : size
}
