import { getConfigFromURL } from "./getConfigFromURL"
import { isArray } from "util"
import { safeWindow } from "framer-motion"

function joinPaths(paths: string[]): string {
    let res = ""
    for (const p of paths) {
        if (!p) continue
        if (res.length > 0 && !res.endsWith("/")) {
            res += "/"
        }
        if (isArray(p)) {
            res += joinPaths(p as any)
        } else {
            res += encodeURIComponent(p).replace(/%2F/g, "/")
        }
    }
    return res
}

/** @internal */
export function calculateServerURL(windowURLString = safeWindow.location.href): string {
    const windowURL = parseURL(windowURLString)
    let projectURL: URL | null = null

    // Check to see if we're getting the "motion" window mock, in which case
    // we need to return the Framer project server url here for server side
    // rendering in Framer X.
    if (!windowURL) {
        return "http://localhost:4567"
    }

    // Otherwise try and find the project URL from the Vekter canvas url
    const projectURLIndex = windowURL.hash.lastIndexOf("http:")
    if (projectURLIndex > 0) {
        projectURL = new URL(windowURL.hash.slice(projectURLIndex))
        if (projectURL) {
            return projectURL.origin
        }
    }

    // Otherwise try the query string params from Vekter Preview URL
    projectURL = parseURL(getConfigFromURL(windowURLString).projectURL)
    if (projectURL) {
        return projectURL.origin
    }

    // At this point we're likely in an exported project.
    const supportedProtocols = new Set(["file:", "http:", "https:"])
    if (supportedProtocols.has(windowURL.protocol)) {
        // If we're outside of Framer the project could be hosted in a
        // subdirectory. In this case we want the projectURL to be relative
        // to the current file.
        projectURL = parseURL("./", windowURL.href)
        if (projectURL) {
            return projectURL.href.replace(/\/$/, "")
        }
    }

    // Fallback to relative paths.
    const fallback = ""

    // tslint:disable-next-line:no-console
    console.warn("Unable to figure out server base address, using fallback:", fallback)
    return fallback
}

function parseURL(url: string, base?: string): URL | null {
    try {
        return new URL(url, base)
    } catch (err) {
        return null
    }
}

let __cachedWebBase: string | null = null
function cachedServerURL(): string {
    if (!__cachedWebBase) {
        __cachedWebBase = calculateServerURL()
    }
    return __cachedWebBase
}

/**
 * @internal
 */
export function serverURL(...paths: string[]) {
    const path = joinPaths(paths)

    // test if it already has a protocol:server style
    const match = /^(\w+)%3A(.*)$/.exec(path)
    if (match) {
        return match[1] + ":" + match[2]
    }

    let server = cachedServerURL()
    // If we have no server url, use a relative path instead of absolute.
    if (server === "") {
        server = "."
    }

    if (path.startsWith("/")) {
        return server + path
    }
    return server + "/" + path
}
