import * as React from "react"
import { LinearGradientElementProperties, RadialGradientElementProperties } from "../utils/elementPropertiesForGradient"

export class LinearGradientElement extends React.Component<LinearGradientElementProperties, {}> {
    render() {
        return (
            <linearGradient id={this.props.id} gradientTransform={`rotate(${this.props.angle}, 0.5, 0.5)`}>
                <stop offset="0" stopColor={this.props.startColor} stopOpacity={this.props.startAlpha} />
                <stop offset="1" stopColor={this.props.stopColor} stopOpacity={this.props.stopAlpha} />
            </linearGradient>
        )
    }
}

export class RadialGradientElement extends React.Component<RadialGradientElementProperties, {}> {
    render() {
        const {
            centerAnchorX,
            centerAnchorY,
            id,
            widthFactor,
            heightFactor,
            startAlpha,
            startColor,
            stopAlpha,
            stopColor,
        } = this.props
        return (
            <radialGradient
                id={id}
                cy={centerAnchorY}
                cx={centerAnchorX}
                r={widthFactor}
                gradientTransform={`translate(${centerAnchorX}, ${centerAnchorY}) scale(1 ${heightFactor /
                    widthFactor}) translate(-${centerAnchorX}, -${centerAnchorY})`}
            >
                <stop offset="0" stopColor={startColor} stopOpacity={startAlpha} />
                <stop offset="1" stopColor={stopColor} stopOpacity={stopAlpha} />
            </radialGradient>
        )
    }
}
